.services__text {
    margin-bottom: 32px;
}

.services__list {
    @media screen and (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
    }
}

.services__item {
    padding: 12;
    background-color: $secondary-bc-color;
    border-radius: 16px;


    &:not(:last-child) {
        @media screen and (max-width: 767px) {
            margin-bottom: 10px;
        }
    }

    @media screen and (min-width: 768px) {
        width: calc((100% - 16px) / 2);
        display: flex;
        flex-direction: column;
        margin-right: 16px;
        margin-bottom: 16px;
        padding: 24;
    }

    @media screen and (min-width: 1280px) {
        width: calc((100% - 32px) / 3);
    }

    cursor: pointer;

    transition-property: box-shadow;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:nth-child(2n) {
        @media screen and (min-width: 768px) and (max-width: 1279px) {
            margin-right: 0;
        }
    }

    &:nth-child(3n) {
        @media screen and (min-width: 1280px) {
            margin-right: 0;
        }
    }

    &:last-child {
        @media screen and (min-width: 768px) and (max-width: 1279px) {
            margin-bottom: 0;
        }
    }

    &:nth-last-child(-n + 3) {
        @media screen and (min-width: 1280px) {
            margin-bottom: 0;
        }
    }

    &:hover {
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
    }

    &:focus {
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
    }
}

.services__title {
    color: $title-text-color;
    font-size: 18px;
    line-height: 1.33;
    font-weight: 400;
    margin-bottom: 12px;

    @media screen and (min-width: 768px) {
        font-size: 24px;
        line-height: 1.16;
        letter-spacing: 0.24px;
        margin-bottom: 16px;
    }
}

.services__img {
    width: 380px;
    border-radius: 12px;

    @media screen and (min-width: 768px) {
        width: 256px;
    }
    
    @media screen and (min-width: 1280px) {
        width: 341px;
    }
}