  height: 44px;
  color: #161616;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 14px 20px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  transition: color .3s linear, background-color .3s linear;
  display: inline-flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

